export class AuthService {
    keycloakInstance;

    constructor(keycloak: any) {
        this.keycloakInstance = keycloak;
    }

    userInfo() {
        return this.keycloakInstance.loadUserProfile();
    }

    getInstance() {
        return this.keycloakInstance;
    }

    getTokens() {
        return {
            token: this.keycloakInstance.token,
            refreshToken: this.keycloakInstance.refreshToken,
        };
    }

    logout() {
        return this.keycloakInstance.logout();
    }
}

const initOptions = {
    url: "http://localhost:8080/auth",
    realm: "emprender",
    clientId: "muii-digital-credit" + (process.env.NODE_ENV === "production" ? '': '-dev'),
    onLoad: "check-sso",
};

export async function initKeycloak() {
    await loadScript(
        `${initOptions.url}/js/keycloak.js`,
        "keycloak",
        "text/javascript"
    );

    const _keycloakClass = (window as any)["Keycloak"];
    const keycloak = _keycloakClass(initOptions);

    return keycloak.init({ onLoad: initOptions.onLoad }).then((auth: any) => {
        if (!auth) {
            // window.location.reload();
            const registerURL = `${initOptions.url}/realms/${initOptions.realm}/protocol/openid-connect/registrations?client_id=${initOptions.clientId}&response_mode=fragment&response_type=code&redirect_uri=${window.location}`;
            window.location.assign(registerURL);
            throw new Error("Not authenticated");
        }

        //Token Refresh
        setInterval(() => {
            keycloak
                .updateToken(70)
                .then((refreshed: false) => {
                    if (refreshed) {
                        console.info("Token refreshed" + refreshed);
                    } else {
                        console.warn(
                            "Token not refreshed, valid for " +
                            Math.round(
                                keycloak.tokenParsed.exp +
                                keycloak.timeSkew -
                                new Date().getTime() / 1000
                            ) +
                            " seconds"
                        );
                    }
                })
                .catch(() => {
                    console.error("Failed to refresh token");
                });
        }, 6000);

        console.log("Authenticated");
        return Promise.resolve({ auth, keycloak });
    });
}

function loadScript(url: string, id: string, type: string) {
    return new Promise((resolve) => {
        document.body.appendChild(
            Object.assign(document.createElement("script"), {
                type: type,
                async: true,
                defer: true,
                id: id,
                src: url,
                onload: resolve,
            })
        );
    });
}
